.buy {
  display: inline-block;
  padding: 16px 85px;
  font-family: "Globerx bold", "Arial", sans-serif;
  font-size: 16px;
  line-height: 24px;
  text-align: center;
  color: $white;
  background-color: $blue;
  border-width: 0;
  border-radius: 29px;
  text-transform: uppercase;
  box-shadow: 11px 14px 31px 7px rgba(0, 0, 0, 0.13);
  cursor: pointer;

  &--order {
    width: 310px;
    height: 76px;
    border-radius: 38px;
  }
}
